<template>
  <div class="wrapper">
    <cHeader/>
    <transition name="fade">
      <main class="c_main" v-if="show">
        <div class="pf_item_data">
          <div class="pf_item_img">
            <picture>
              <source
                media="(min-width: 601px)"
                :srcset="itemData.thumb01"
              >
              <img :src="itemData.thumb02">
            </picture>
          </div>
          <div class="pf_item_title">
            <span class="title_midashi">title</span>
            {{ itemData.title }}
          </div>
          <div class="pf_item_url">
            <span class="title_midashi">url</span>
            <a
              :href="itemData.url"
              target="_blank"
            >
             {{ itemData.url }}
             <font-awesome-icon icon="external-link-alt" class="fa-fw" />
            </a>
          </div>
          <div class="pf_item_date">
            <span class="title_midashi">released</span>
            {{ itemData.date }}
          </div>
          <div class="pf_item_position">
            <span class="title_midashi">position</span>
            {{ itemData.position }}
          </div>
        </div>
        <router-link to="/item/" class="pf_back">back</router-link>
      </main>
    </transition>
    <footer class="c_footer">
    </footer>
  </div>
</template>

<script>
import cHeader from '../components/header.vue'

export default {
  name: 'item',
  components: {
    cHeader
  },
  data: function (){
    return {
      show: false
    }
  },
  computed: {
    itemData () {
      const itemId = this.$route.params.id
      const data = this.$store.getters.items.find(a => ( a.p_id === itemId ))
      return data
    }
  },
  mounted: function() {
    document.title = this.itemData.title + 'ï½œMy Portfolio'
    let lState = this.$store.getters.logined
    if(!lState){
      this.$router.push(`/`)
    }
    this.show = true
    window.scrollTo( 0, 0 );
  }
}
</script>

